.courses{
  /* border: 1px solid lightblue; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}
.course_1{
  position: relative;
  top: 0px;
  left: 160px;
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
  -webkit-transition: -webkit-transform .25s ease-in-out;
  transition: -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
  -webkit-transform-origin: left top;
          transform-origin: left top;
}
.course_2{
  position: relative;
  top: -10px;
  left: 20px;
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
  -webkit-transition: -webkit-transform .25s ease-in-out;
  transition: -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
  /* transition: .3s; */
}
.course_3{
  position: relative;
  left: -160px;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  -webkit-transition: -webkit-transform .25s ease-in-out;
  transition: -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
  -webkit-transform-origin: left top;
          transform-origin: left top;
}
.courses:hover .course_1{
  -webkit-transform: translate3d(60px, -170px, 0px) rotateZ(0deg);
          transform: translate3d(60px, -170px, 0px) rotateZ(0deg);
}
.courses:hover .course_2{
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg);
}
.courses:hover .course_3{
  -webkit-transform: translate3d(-20px, 150px, 0px) rotateZ(0deg);
          transform: translate3d(-20px, 150px, 0px) rotateZ(0deg);
}
.courses-button {
